.top{
    width: 100%;
    height: 50px;
    background-color: white;
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    font-family: 'Josefin Sans', sans-serif;
    z-index: 999;
}

.topleft,.topright{
flex:3;
display: flex;
align-items: center;
justify-content: center;
}

.topcenter{
    flex:6;
}

.topicon{
   font-size: 20px;
   margin-right: 10px;
   cursor: pointer;
   color: #444; 
}
.topimg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    cursor: pointer;
}

.toplist{
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;
}

.toplistitem{
    margin-right: 20px;
    font-size: 18px;
    font-weight: 300;
    cursor: pointer;
}


.topsearch{
    font-size: 18px;
    color: #666;
    cursor: pointer;
    margin-left: 15px;
}